.header {
    display: flex;
    background-color: saddlebrown;
    color: white;
    border: 0px;
    margin: 0px;
    padding: 0px;
    width: 100%;
    height: 12%;
    align-items: center;
}

.main-container {
    display: flex;
}

.main-area {
    background-color: black;
    height: 100%;
    width: auto;
    flex: 1;
    color: white;
    text-align: center;
}

.chat-container {
    overflow-y: auto;
    height: 60vh;
}

.sent-message {
    background-color: rgb(206, 178, 142);
    display: flex;
    margin: 10px;
    padding: 3px;
    justify-content: right;
    align-items: center;
}

.received-message {
    background-color: rgb(206, 178, 142);
    display: flex;
    margin: 10px;
    padding: 3px;
    justify-content: left;
    align-items: center;
}

.chat-user {
    padding-right: 8px;
}

.create-message-div {
    display: flex;
    background-color: rgb(206, 178, 142);
    justify-content: right;
}

.send-message-btn {
    cursor: pointer;
}

.new-message {
    flex: 3;
    background-color: rgb(163, 136, 100);
    color: black;
    font-size: 18px;
}

a {
    color: white;
    text-decoration: none;
}

body, html {
    margin: 0px;
    padding: 0px;
    border: 0px;
    background-color: black;
}